@import-normalize;
@import './imports';

html {
    background: $background-color;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: border-box;
}
body {
    margin:0;
    padding:0;
    direction: rtl;
    font-family: Heebo-Regular,'Arial', sans-serif;
    background: $background-color;
    -webkit-font-smoothing: antialiased;
    color:$text-color;
}
input {
    font-family: Heebo-Regular;
    border-width: 0 0 1px 0;
    border-color: 1px solid $grey;
}
input:focus  {
    border-color: inherit;
}
textarea {
    display: block;
    outline: none;
    resize: none;
    overflow:auto;
    border: 1px solid lightgray;
    border-radius: 4px;
    width:100%;
}
input:focus, textarea:focus {
    outline: none;
}
button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}
button:focus, button:hover, :focus {
    outline: none;
}
a, a:focus, a:active {
    color: inherit;
    text-decoration: none;
}
ul {
    list-style: none;
}
img {
    display: block;
    width: 100%;
}

/* Fix webkit autofill background - Change the white to any color */
input:-webkit-autofill {
    box-shadow: 0 0 0 30px #ffffff inset;
}

h1 {
    font-size: vw_size(24,$mobile);
}
h2 {}
h3 {}
h4 {}
h5 {}
h6 {}

@import './device-state';
@import './styles';